@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Poppins:wght@300;400;500&display=swap);
*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  scroll-behavior: smooth;
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  background-color: #f6f6f6;
  margin: 0;
  padding: 0;
  font-family: "Poppins", BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media screen and (max-width: 500px) {
  body {
    text-align: center;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav{visibility:hidden;height:4.8rem;padding:0px 6rem;background-color:#222;color:#fff;display:flex;align-items:center;justify-content:space-between;position:fixed;top:0;width:100%;overflow:hidden;z-index:100}@media screen and (max-width: 500px){nav{justify-content:center;height:3.8rem}}nav h4{font-size:1.8rem;margin:0}@media screen and (max-width: 500px){nav h4{display:none}}nav ul{list-style:none;display:flex;margin:0;padding:0}nav ul li{margin-right:2rem;font-size:1.5rem;font-weight:400}nav ul li a{color:#fff;font-size:1.8rem;text-decoration:none}@media screen and (max-width: 500px){nav ul li a{font-size:1.5rem}}nav ul li:last-child{margin-right:0}main{color:white;visibility:hidden;grid-row-start:1;padding-top:6rem;grid-column:center-start / center-end;display:flex;align-items:center;justify-content:center}@media screen and (max-width: 500px){main{flex-direction:column;padding:6rem 0;padding-bottom:3.2rem}}main a{color:white;text-decoration:none}main .hero-a{flex:0 0 60%;opacity:1}@media screen and (max-width: 500px){main .hero-a{order:2;padding-top:0}}main .hero-a h1{margin:0}main .hero-a h4{font-weight:300;margin-top:2rem;margin-bottom:3rem}main .hero-a p{margin-top:3rem}main .hero-b{flex:0 0 40%;flex-direction:column;display:flex;justify-content:flex-start;align-items:center}@media screen and (max-width: 500px){main .hero-b{order:1}}main .hero-b .avatar{background-image:url(/curriculum/static/media/avatarb.f4d07653.jpeg);background-position:center;background-size:cover;height:40rem;width:40rem;background-color:#fff;border-radius:50%}@media screen and (max-width: 500px){main .hero-b .avatar{height:25rem;width:25rem;margin-bottom:4rem}}.proyectos{grid-column:full-start / full-end;background-image:linear-gradient(to top, #f6f6f6 0%, #eddafc 100%);color:#222;padding-bottom:4.8rem}.proyectos h3{margin-left:6rem;color:#222}@media screen and (max-width: 500px){.proyectos h3{margin-left:0}}.proyectos .gridp{margin:0 6rem;display:grid;grid-gap:3rem;grid-template-columns